.films-details {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 3fr 0.5fr 0.25fr 1fr 0.25fr 1fr repeat(3, 0.25fr) 5fr;
  color: white;
  gap: 0.25rem 1rem;
  padding: 2rem;
}

.films-details > img {
  grid-column: 1 / -1;
  margin: auto;
}

.films-details > h2 {
  grid-column: 1 / -1;
  grid-row: 2 / 3;
  text-align: center;
}

.films-details > h3 {
  grid-column: 1 / -1;
  grid-row: 3 / 4;
  text-align: center;
}

.films-details > .films-genres-list {
  grid-column: 1 / -1;
  grid-row: 4 / 5;
}

.films-details > .overview-title {
  grid-column: 1 / -1;
  text-align: center;
  grid-row: 5 / 6;
}

.films-details > .overview {
  grid-column: 1 / -1;
  grid-row: 6 / 7;
  text-align: center;
}

.films-details > .release-date-title {
  text-align: right;
}

.films-details > .imdb-title {
  text-align: right;
}

.films-details > .trailer-title {
  grid-column: 1 / -1;
  text-align: center;
}

.films-details > .trailer {
  grid-column: 1 / -1;
  margin: auto;
  width: 60%;
  height: 90%;
}
